@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Black+Han+Sans|Oswald");
@font-face {
  font-family: "social";
  src: url("../assets/fonts/myicons.eot");
  src: url("../assets/fonts/myicons.?#iefix") format("embedded-opentype"), url("../assets/fonts/myicons.woff") format("woff"), url("../assets/fonts/myicons.ttf") format("truetype"), url("../assets/fonts/myicons.#myicons") format("svg");
  font-weight: normal;
  font-style: normal; }

.icon {
  font-family: "social"; }
  .icon:before {
    /* Some common icon stuff */ }
  .icon.icon--arm-up:before {
    content: ""; }
  .icon.icon--call:before {
    content: ""; }
  .icon.icon--sent-mail:before {
    content: ""; }
  .icon.icon--speech-bubble:before {
    content: ""; }

* {
  box-sizing: border-box; }

body {
  font-family: 'Oswald', sans-serif; }

input,
textarea {
  outline: none;
  border: none;
  box-shadow: 0 0 2px black;
  transition: 0.3s;
  resize: none; }

.icon {
  font-size: 0px;
  color: #61b3de;
  position: absolute;
  left: 10px;
  bottom: 0px;
  transition: 0.3s; }
  .icon--call {
    bottom: -4px; }
  .icon--sent-mail {
    bottom: -10px; }

.title--contact-form {
  position: absolute;
  top: 0;
  font-size: 20px; }

.button {
  cursor: pointer;
  outline: none;
  position: relative;
  transition: 0.3s; }
  .button--send {
    background-color: #666;
    color: #fff;
    align-self: center;
    padding: 5px 20px;
    font-size: 24px;
    border: none;
    letter-spacing: 1px;
    z-index: 0;
    overflow: hidden;
    font-weight: bold;
    margin-top: 20px; }
    .button--send:before {
      content: "";
      position: absolute;
      width: 0;
      height: 100%;
      background-color: #fff;
      left: 50%;
      top: 0;
      z-index: -1;
      transform: translate(-50%) skewX(45deg);
      transition: 0.3s; }
    .button--send:hover {
      color: black; }
      .button--send:hover:before {
        width: 100%; }
    .button--send:active {
      color: black; }
      .button--send:active:before {
        width: 140%; }

.label {
  position: relative; }
  .label--services {
    width: 40%;
    cursor: pointer;
    height: 26px; }
  .label--contacts {
    margin-bottom: 10px; }
  .label--textarea {
    width: 100%; }
  .label__text {
    font-size: 18px; }

.form--contact {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  border: 2px solid black;
  margin: 50px auto;
  padding: 15px; }
  .form--contact .row {
    display: flex;
    justify-content: space-between; }
    .form--contact .row .contacts {
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: flex-start; }
      .form--contact .row .contacts input {
        padding: 5px 10px;
        font-size: 18px;
        backface-visibility: hidden; }
        .form--contact .row .contacts input:focus {
          box-shadow: none;
          padding-left: 40px; }
          .form--contact .row .contacts input:focus ~ .icon {
            font-size: 26px; }
            .form--contact .row .contacts input:focus ~ .icon--call {
              font-size: 30px; }
            .form--contact .row .contacts input:focus ~ .icon--sent-mail {
              font-size: 50px;
              bottom: -22px; }
          .form--contact .row .contacts input:focus + .focus-border:before, .form--contact .row .contacts input:focus + .focus-border:after {
            content: "";
            position: absolute;
            height: 2px;
            background-color: #61b3de; }
          .form--contact .row .contacts input:focus + .focus-border:before {
            top: 24px;
            animation: .2s top_border_focus forwards; }

@keyframes top_border_focus {
  from {
    width: 0; }
  to {
    width: 100%;
    transition: 0.3s; } }
          .form--contact .row .contacts input:focus + .focus-border:after {
            right: 0;
            animation: .2s bottom_border_focus .4s forwards; }

@keyframes bottom_border_focus {
  from {
    width: 0; }
  to {
    width: 100%; } }
          .form--contact .row .contacts input:focus + .focus-border i:before, .form--contact .row .contacts input:focus + .focus-border i:after {
            content: "";
            position: absolute;
            background-color: #61b3de;
            width: 2px;
            z-index: 1; }
          .form--contact .row .contacts input:focus + .focus-border i:after {
            right: 0;
            top: 26px;
            animation: .2s right_border_focus .2s forwards; }

@keyframes right_border_focus {
  from {
    height: 0; }
  to {
    height: 32px; } }
          .form--contact .row .contacts input:focus + .focus-border i:before {
            left: 0;
            bottom: 0;
            animation: .2s left_border_focus .6s forwards; }

@keyframes left_border_focus {
  from {
    height: 0; }
  to {
    height: 32px; } }
    .form--contact .row .services {
      width: 50%;
      display: flex;
      flex-wrap: wrap;
      position: relative;
      padding-top: 50px;
      justify-content: space-between; }
      .form--contact .row .services [type="checkbox"] {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
        appearance: none;
        width: 16px;
        height: 16px;
        border: 1px solid black;
        transition: 0.3s;
        cursor: pointer; }
        .form--contact .row .services [type="checkbox"]:checked {
          transform: translateY(-50%) rotate(360deg); }
          .form--contact .row .services [type="checkbox"]:checked:before {
            content: "\2713";
            position: absolute;
            font-weight: bold;
            font-size: 18px;
            top: -6px;
            opacity: 0;
            animation: 1s checkmark .3s forwards; }

@keyframes checkmark {
  0 {
    transform: scale(1);
    opacity: 0; }
  50% {
    transform: scale(1.6);
    opacity: 1; }
  100% {
    transform: scale(1);
    opacity: 1; } }
  .form--contact .textarea {
    width: 100%;
    padding: 5px 10px;
    font-size: 18px; }
    .form--contact .textarea:focus {
      box-shadow: 0 0 2px 2px #61b3de; }
